import React, {useState, useEffect} from 'react';
import './App.css';

const API_URL = 'https://api.icndb.com/jokes/random?category={history}';
const API_URL2 = 'https://api.icndb.com/jokes/random?category={animal}';
const API_URL3 = 'https://api.icndb.com/jokes/random?category={science}';
const API_URL4 = 'https://api.icndb.com/jokes/random?category={explicit}';
const API_URL5 = 'https://api.icndb.com/jokes/random?category={nerdy}';

function App() {
    const [joke, setJoke] = useState('');

    const generateJoke = () => {
        fetch(API_URL)
        .then(res => res.json())
        .then(data => setJoke(data.value.joke))

    }

    useEffect(() => {
        generateJoke();   

    }, [])

    const categoryJoke = () => {
        fetch(API_URL2)
        .then(res => res.json())
        .then(data => setJoke(data.value.joke))

    }
    useEffect(() => {
        categoryJoke();   

    }, [])

    const scienceJoke = () => {
        fetch(API_URL3)
        .then(res => res.json())
        .then(data => setJoke(data.value.joke))

    }

    useEffect(() => {
        scienceJoke();   

    }, [])

    const explicitJoke = () => {
        fetch(API_URL4)
        .then(res => res.json())
        .then(data => setJoke(data.value.joke))

    }

    useEffect(() => {
        explicitJoke();   

    }, [])

    const nerdyJoke = () => {
        fetch(API_URL5)
        .then(res => res.json())
        .then(data => setJoke(data.value.joke))

    }

    useEffect(() => {
        nerdyJoke();   

    }, [])




    return ( 
    
         <div class="box">
           <h2>Chuck Norris APP</h2>
           <p dangerouslySetInnerHTML = {{__html: joke}} />
           <button onClick = {generateJoke}>Get a history Joke</button>
           <button onClick = {scienceJoke}>Get a science Joke</button>
           <button onClick = {explicitJoke}>Get an explicit Joke</button>
           <button onClick = {nerdyJoke}>Get an nerdy Joke</button>
           

         </div>
    );
}

export default App;